<script>
export default {
	name: 'evaluate',
	data() {
		return {
			MessageShow: false,
			tab: false,
			gradearr: [[false, false, false, false, false], [false, false, false, false, false], [false, false, false, false, false]],
			suggest: [false, false, false, false, false, false]
		};
	},
	methods: {
		messageShow() {
			if (this.MessageShow == false) {
				this.MessageShow = true;
			} else {
				this.MessageShow = false;
			}
		},
		back() {
			this.$router.go(-1);
		},
		quit() {
			this.$router.push({
				path: `/`
			});
			return false;
		},
		evaluatetab(x) {
			this.tab = x;
		},
		tastestar(x, i) {
			var arr = [];
			if (x == 0) {
				arr = [true, false, false, false, false];
				this.gradearr[i] = arr;
			}
			if (x == 1) {
				arr = [true, true, false, false, false];
				this.gradearr[i] = arr;
			}
			if (x == 2) {
				arr = [true, true, true, false, false];
				this.gradearr[i] = arr;
			}
			if (x == 3) {
				arr = [true, true, true, true, false];
				this.gradearr[i] = arr;
			}
			if (x == 4) {
				arr = [true, true, true, true, true];
				this.gradearr[i] = arr;
			}
		},
		sugcheckbox(i) {
			this.suggest[i] = !this.suggest[i];
		}
	}
};
</script>

